import React from 'react'
import styles from './Auth.module.css';
import home from '../img/home.jpg';
import grouphome from '../img/grouphome.jpg';
import mahjong from '../img/mahjong.jpg';
import battle from '../img/battle.jpg';
import member from '../img/profiledetail.jpg';
import recode from '../img/recode.jpg';
import editrecode from '../img/editrecode.jpg';
import setting from '../img/setting.jpg';
const HowToUse:React.FC = () => {
    return (
        <>
            <div className={styles.auth_what_container}>
            <div className={styles.auth_how_to}>
                <h2 className={styles.auth_how_to_h2}>使い方</h2>
            </div>
            <div className={styles.auth_how_to_container}>
                <div className={styles.auth_how_to_body}>
                    <div>
                        <p className={styles.how_to_p}>1. ログイン又はアカウントを作成します</p>
                    </div>
                    <div>
                        <p className={styles.how_to_p}>2. グループを作成します。</p>
                        <p className={styles.how_to_p_2}>作成せずに友人などからの招待でグループに参加することも可能です。</p>
                        <p className={styles.how_to_p_2}>グループは何個でも作成、参加できます。</p>
                        <img src={home} className={styles.how_to_image_login} alt="img"/>
                    </div>
                    <div>
                        <p className={styles.how_to_p}>3. グループのホームに移動します</p>
                        <p className={styles.how_to_p_2}>シェアボタンからグループをシェアできます。</p>
                        <img src={grouphome} className={styles.how_to_image_login} alt="img"/>
                        <p className={styles.how_to_p}>グループホームでの各機能紹介</p>
                        <div　className={styles.how_to_description_div}>
                            <p className={styles.how_to_p_title}>3-1.対局</p>
                            <img src={battle} className={styles.how_to_image_login} alt="img"/>
                            <p　className={styles.how_to_p_2}>対局ボタンを押すことで対局画面へと移行します。</p>
                            <p　className={styles.how_to_p_2}>対局者4人を選択します。この際選択順で東南西北が決定します。</p>
                            <p　className={styles.how_to_p_2}>合計得点が100000点になるように入力することで記録できます。</p>
                            <p　className={styles.how_to_p_2}>ウマは5-10、10-20、10-30から選択します。</p>
                        </div>
                        <div　className={styles.how_to_description_div}>
                            <p className={styles.how_to_p_title}>3-2.メンバー</p>
                            <img src={member} className={styles.how_to_image_login} alt="img"/>
                            <p　className={styles.how_to_p_2}>グループに参加しているメンバーが表示されます。</p>
                            <p　className={styles.how_to_p_2}>アイコンをクリックすることでメンバーごとの成績が表示できます。</p>
                        </div>
                        <div　className={styles.how_to_description_div}>
                            <p className={styles.how_to_p_title}>3-3.対局記録</p>
                            <img src={recode} width="340px" alt="img"/>
                            <p　className={styles.how_to_p_2}>対局記録を表示します。</p>
                            <p　className={styles.how_to_p_2}>日付を選択することで表示したい記録の区間を選択できます。</p>
                            <p　className={styles.how_to_p_2}>日付を選択するしていない場合すべての累計が表示されます。</p>
                            <img src={editrecode} className={styles.how_to_image_login} alt="img"/>
                            <p　className={styles.how_to_p_2}>対局記録を選択することで記録の編集・削除を行えます。</p>
                            <p　className={styles.how_to_p_2}>編集画面では得点の合計が0になるようにしてください。</p>
                        </div>
                        <div　className={styles.how_to_description_div}>
                            <p className={styles.how_to_p_title}>3-4.設定</p>
                            <img src={setting} className={styles.how_to_image_login} alt="img"/>
                            <p　className={styles.how_to_p_2}>設定モーダルを開きます。</p>
                            <p　className={styles.how_to_p_2}>画像、名前、紹介文、パスワードを変更できます。</p>
                            <p　className={styles.how_to_p_2}>名前、パスワードは30文字、紹介文は200文字まで設定できます。</p>
                            <p　className={styles.how_to_p_2}>グループを抜けるをクリックするとグループを抜けることができます。グループを抜けてもデータは残るので再度グループに参加することで引き続き記録できます。</p>
                        </div>
                    </div>
                    <div>
                        <p className={styles.how_to_p}>4. ルールとマナーを守って楽しく対局しよう!!</p>
                        <img src={mahjong} className={styles.how_to_image_login}alt="img"/>
                    </div>
                    <br/>
                    <br/>
                    <div>
                        <p className={styles.how_to_p_3}>※オカ(トップ賞)は+20です。</p>
                        <p className={styles.how_to_p_3}>※レートの初期値は1500です。</p>
                        <p className={styles.how_to_p_3}>※ヘッダーのユーザーアイコンでユーザー名等の変更が行えます。</p>
                        <p className={styles.how_to_p_3}>※ヘッダーのユーザーアイコンからログアウトできます。</p>
                    </div>
                </div>
            </div>
            </div>
        </>
    )
}

export default HowToUse
