import React from 'react'
import styles from './Auth.module.css';
const WhatIs:React.FC = () => {
    return (
        <>
        <div className={styles.auth_what_container}>
            <div className={styles.auth_what}>
                <h2 className={styles.auth_what_h2}>グループ麻雀レコードとは</h2>
            </div>
            <p className={styles.auth_what_p}>グループを作成してグループごとに麻雀の成績を管理することができます。</p>
            <p className={styles.auth_what_p}>グループ数に上限はないため何個でもグループを作成、参加することができます。</p>
            <p className={styles.auth_what_p}>グループごとに麻雀の結果によるレートが存在します。</p>
            <p className={styles.auth_what_p}>そのためグループ内で誰が一番勝っているか一目瞭然！</p>
            <p className={styles.auth_what_p}>麻雀の成績は累計、1日、1週間、と好きな区間で集計結果を表示できます。</p>
        </div>
        </>
    )
}

export default WhatIs
